<template lang="pug">
  loading-container.router-view-content(:loading="!round || !adjudicator", no_item_text="Fail to load round/adjudicator data")
    section.page-header(v-if="round && adjudicator")
      h1 {{ adjudicator.name }}
      h3 {{ round.name }}
    section(v-if="round && adjudicator")
      el-steps(:active="current_step", finish-status="success", center)
        el-step(title="Speaker")
        el-step(title="Score")
        el-step(title="Winner")
        el-step(title="Check")
    router-view(v-if="round && adjudicator && score_sheet", :score_sheet="score_sheet")
</template>

<script>
/* @flow */
import { mapState, mapGetters, mapActions, mapMutations } from 'vuex'
import loading_container from 'components/loading-container'

export default {
  props: ['r_str', 'from_id_str'],
  components: {
    'loading-container': loading_container
  },
  data () {
    return {
      steps: ['speaker', 'score', 'winner', 'check', 'done']
    }
  },
  computed: {
    current_step () {
      return this.steps.findIndex(step => step === this.$route.name)
    },
    round () {
      return this.target_round
    },
    adjudicator () {
      return this.entity_by_id(this.from_id_str)
    },
    score_sheet () {
      return this.score_sheet_by_id(this.from_id_str)
    },
    ...mapState([
      'auth',
      'loading'
    ]),
    ...mapGetters([
      'target_round',
      'entity_by_id',
      'score_sheet_by_id',
      'style'
    ])
  },
  methods: {
    ...mapMutations('ballot', [
      'init_result'
    ]),
  },
  mounted () {
    let role_names = {
      gov: this.style.roles.gov.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => r.abbr),
      opp: this.style.roles.opp.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => r.abbr),
    }
    let score_default = {
      gov: this.style.range.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => r.value.default),
      opp: this.style.range.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => r.value.default)
    }
    let sub_prize_default = {
      gov: this.style.roles.gov.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => false),
      opp: this.style.roles.opp.slice().sort((a, b) => a.order > b.order ? 1 : -1).map(r => false)
    }
    let payload = {
      len: this.style.score_weights.length,
      score_default,
      sub_prize_default
    }
    this.init_result(payload)
  },
  watch: {
    '$route': function () {
      window.scrollTo(0, 0)
    }
  }
}
</script>



